import L from 'leaflet'

import type {
	ClientLand,
	ClientLandPlot,
	ClientLandShortPlot,
	ClientShortLand,
	DistrictLand,
	RegionLand,
} from '../fixtures'

export function regionsAdapter(regions: RegionLand[]): RegionLand[] {
	return regions.map((region) => ({
		...region,
		geometry_rings: geometriesAdapter1(region.geometry_rings),
	}))
}

export function districtsAdapter(districts: DistrictLand[]): DistrictLand[] {
	return districts.map((district) => ({
		...district,
		geometry_rings: geometriesAdapter1(district.geometry_rings),
	}))
}

export function clientsPlotsAdapter(clients: ClientShortLand[]): ClientLandShortPlot[] {
	const plots: ClientLandShortPlot[] = []

	clients.forEach((client) => {
		client.linkPlot.forEach((plot) => {
			plots.push({
				...plot,
				geometry_rings: geometriesAdapter2(plot.geometry_rings),
				guid: client.guid,
				clientId: client.clientId,
				regionId: client.regionId,
				districtId: client.districtId,
			})
		})
	})

	return plots
}

export function clientPlotsByRegionAdapter(client: ClientLand): ClientLandPlot[] {
	const plots: ClientLandPlot[] = []

	client.linkPlot.forEach((plot) => {
		plots.push({
			...plot,
			geometry_rings: geometriesAdapter2(plot.geometry_rings),
			guid: client.guid,
			clientId: client.clientId,
			clientName: client.clientName,
			clientBin: client.clientBin,
			clientAddress: client.clientAddress,
			clientAction: client.clientAction,
			regionId: client.regionId,
			districtId: client.districtId,
		})
	})

	return plots
}

export function clientsPlotsByCulturesAdapter(plots: ClientLandPlot[]): ClientLandPlot[] {
	return plots.map((plot) => ({
		...plot,
		geometry_rings: geometriesAdapter2(plot.geometry_rings),
	}))
}

export function geometriesAdapter1(geometries: number[][][]): number[][][] {
	try {
		const proj = L.CRS.EPSG3857

		if (geometries) {
			const newGeometries = geometries[0]!.map((geometry: any) => {
				const newGeometry = proj.unproject(new L.Point(geometry[0], geometry[1]))
				return [newGeometry.lat, newGeometry.lng]
			})

			return [newGeometries]
		}

		return geometries
	} catch (err) {
		throw err
	}
}

export function geometriesAdapter2(geometries: number[][][]): number[][][] {
	return [geometries['0']!.map((latlng) => [latlng[1]!, latlng[0]!])]
}
